<template>
    <div class="equipmentManagement">
        <div class="search">
            <el-input placeholder="请输入设备名称" v-model="search.name" clearable size="small"></el-input>
            <!-- <el-input placeholder="请输入设备所在位置" v-model="search.address" clearable size="small"></el-input> -->
            <el-select clearable size="small" placeholder="请选择设备类型" v-model="search.device_id">
                <el-option v-for="item in deviceType" :label="item.name" :value="item.id" :key="item.id"></el-option>
            </el-select>
            <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
            <div class="add">
                <el-button size="small" type="primary" icon="el-icon-plus" class="btn_add" @click="add">新增开关任务</el-button>
            </div>
        </div>
        <div class="home_table">
            <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                <template v-for="(item, index) in tableHead">
                    <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" v-if="item.prop=='status'" :min-width="item.minWidth">
                        <template slot-scope="scope">
                            <el-switch v-model="tableData[scope.$index][item.prop]" @change="statusChange(scope.row,tableData[scope.$index][item.prop])"></el-switch>
                        </template>
                    </el-table-column>
                    <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" v-else-if="item.prop=='time'" :min-width="item.minWidth">
                        <template slot-scope="scope">
                            {{scope.row.start_time+'-'+scope.row.end_time}}
                        </template>
                    </el-table-column>
                    <el-table-column :prop="item.prop" align="center" v-else :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                </template>
                <el-table-column prop="address" label="操作" fixed="right" width="160">
                    <template slot-scope="scope">
                        <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                        <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <!-- 新增设备弹窗 -->
        <el-dialog :title="title" :visible.sync="dialogVisible" v-dialogDrag width="700px" :close-on-click-modal="false" @close='handleClose'>
            <el-form :model="formData" ref="formData" :rules="rule" label-width="110px">
                <el-form-item label="开关任务名称" prop="title">
                    <el-input v-model.trim="formData.title" placeholder="请输入开关任务名称"></el-input>
                </el-form-item>
                <el-form-item label="设备类型" prop="device_type">
                    <el-select v-model="formData.device_type" placeholder="请选择设备类型">
                        <el-option v-for="item in deviceType" :label="item.name" :value="item.id" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="控制周期" prop="period">
                    <el-checkbox-group size="small" v-model="weekBtn" @change="weekChange">
                        <el-checkbox-button label="-1">{{'循环一周'}}</el-checkbox-button>
                    </el-checkbox-group>
                    <el-checkbox-group v-model="formData.period" size="small" @change="checkChange">
                        <el-checkbox-button v-for="item in week" :label="item.id" :key="item.id">{{item.name}}</el-checkbox-button>
                    </el-checkbox-group>
                </el-form-item>
                <el-form-item label="使用时间">
                    <el-time-picker is-range v-model="timeValue" format='HH:mm' value-format='HH:mm' range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间" placeholder="选择时间范围" @change="timeChange">
                    </el-time-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {
    hashApi,
    equipmentSwitchList,
    equipmentSwitchView,
    equipmentSwitchAdd,
    equipmentSwitchEdit,
    equipmentSwitchDel,
    equipmentSwitchInfo,
    equipmentSwitchStatus,
} from '@/api/index';
export default {
    data() {
        return {
            timeValue: null,
            total: 0,
            page: 1,
            pageSize: 15,
            tableHeight: window.innerHeight - 180,
            dialogVisible: false,
            title: '',//新增编辑标题
            search: {
                name: '',
                address: '',
                device_id: '',
            },
            formData: {
                period: [],
                // time: ['', ''],
                device_type: '',
                title: '',
            },
            weekBtn: [],
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '100', },
                { title: '开关任务名称', prop: 'title', minWidth: '120', },
                { title: '设备类型', prop: 'device_type_name', minWidth: '120', },
                { title: '控制周期', prop: 'period_name', minWidth: '180', },
                { title: '使用时间', prop: 'time', minWidth: '160', },
                { title: '创建时间', prop: 'create_time', minWidth: '150', },
                { title: '状态', prop: 'status', minWidth: '100', },
            ],
            tableData: [],
            rule: {
                title: [
                    { required: true, message: '请输入开关任务名称', trigger: 'blur' },
                ],
                device_type: [
                    { required: true, message: '请选择设备类型', trigger: 'change' },
                ],
                period: [
                    { type: 'array', required: true, message: '请选择控制周期', trigger: 'change' },
                ],
                /* time: [
                    { required: true, message: '请选择使用时间', trigger: 'change' },
                ], */
            },
            deviceType: [],//设备类型列表
            hash: '',
            week: [
                { id: '1', name: '星期一', },
                { id: '2', name: '星期二', },
                { id: '3', name: '星期三', },
                { id: '4', name: '星期四', },
                { id: '5', name: '星期五', },
                { id: '6', name: '星期六', },
                { id: '0', name: '星期天', },
            ],
            // cities: ['上海', '北京', '广州', '深圳'],
        };
    },
    mounted() {
        this.getList();
        this.getHash();
        this.resView();
    },
    methods: {
        timeChange(e) {
            this.timeValue = e
        },
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        weekChange(e) {
            if (e.indexOf('-1') != '-1') {
                this.formData.period = ['1', '2', '3', '4', '5', '6', '0'];
            } else {
                this.formData.period = [];
            }
        },
        checkChange(e) {
            if (e.length != 7) {
                this.weekBtn = [];
            }
        },
        async statusChange(e, i) {
            const res = await this.axios.post(equipmentSwitchStatus, { id: e.id, state: i ? 1 : 0, hash: this.hash, });
            if (res.data.code == 1) {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                });
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.getList();
            this.getHash();
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        add() {
            this.title = '新增开关任务';
            this.dialogVisible = true;
            this.resetForm();
        },
        //编辑
        editBtn(row) {
            this.title = '编辑开关任务';
            this.dialogVisible = true;
            this.resetForm();
            this.getInfo(row.id);
        },
        resetForm() {
            this.formData = {
                title: '',
                period: [],
                // time: ['', ''],
                device_type: "",
            };
            this.timeValue = null
            this.weekBtn = [];
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
        handleClose() {
            this.resetForm()
            this.dialogVisible = false;
        },
        async getInfo(id) {
            const res = await this.axios.post(equipmentSwitchInfo, { id: id });
            if (res.data.code == 1) {
                console.log(res.data.data);
                this.formData = res.data.data;
                this.timeValue = [res.data.data.start_time, res.data.data.end_time];
                this.formData.period = res.data.data.period.length > 0 ? res.data.data.period.split(',') : [];
                if (this.formData.period.length == 7) {
                    this.weekBtn = ['-1'];
                } else {
                    this.weekBtn = [];
                }
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                page: this.page,
                pageSize: this.pageSize,
                device_type: this.search.device_id,
                location: this.search.address,
                title: this.search.name,
            }
            const res = await this.axios.post(equipmentSwitchList, data);
            if (res.data.code == 1) {
                res.data.data.data.forEach(e => {
                    if (e.status == 1) {
                        e.status = true;
                    } else if (e.status == 0) {
                        e.status = false;
                    }
                });
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async resView() {
            const res = await this.axios.post(equipmentSwitchView,);
            if (res.data.code == 1) {
                this.deviceType = res.data.data.device_type_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'equipmentSwitch' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        submit() {
            this.$refs['formData'].validate(async valid => {
                if (valid) {
                    if (this.timeValue == null) {
                        this.$message({
                            showClose: true,
                            message: '请选择使用时间',
                            type: 'warning'
                        });
                        return
                    }
                    await this.getHash();
                    let data = {
                        device_type: this.formData.device_type,
                        title: this.formData.title,
                        hash: this.hash,
                        period: this.formData.period.join(','),
                        start_time: this.timeValue[0],
                        end_time: this.timeValue[1],
                    }
                    let api;
                    if (this.title == '新增开关任务') {
                        api = equipmentSwitchAdd
                    } else {
                        api = equipmentSwitchEdit;
                        data.id = this.formData.id;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        delBtn(id) {
            this.$confirm('是否确定删除该设备', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                    };
                    const res = this.axios.post(equipmentSwitchDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    // this.$notify.info({
                    //     title: '取消',
                    //     message: '取消操作'
                    // });
                });
        },
    },
};
</script>

<style lang="scss" scoped>
.search {
    display: flex;
    padding: 12px 25px;
    background: #fff;
    .el-select {
        margin-right: 24px;
    }
    .el-input {
        width: auto;
        margin-right: 24px;
    }
    .el-cascader {
        margin-right: 24px;
    }
    .add {
        flex: 1;
        .btn_add {
            float: right;
        }
        .el-input {
            width: 500px;
        }
    }
}
</style>


